body {
  margin: 0; width: 100%;background-color: rgb(216, 213, 213);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.topic{
  text-align: center;
  background-color: black;
  color: white;
  margin-top: 0;
}

.container-1{
  border: 1px solid black;
  margin: 3% 2%;
  border-radius: 5px;
}

.con-1{
  margin: 1% 5%;
}
.container-2{
  margin: 3% 15%;
  display: flex;
  gap:30%;
  text-align: center;
}
input{
  border: none;
  width: 60px;
  height: 40px;
  border-radius: 10px;
  text-align: center;
}
h6{
  margin: 0;
}

@media (max-width: 600px) {
  .container-2{
    margin: 3% 6%;
    gap: 14%;
  }
}